@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

:root {
  --pri: rgb(0, 177, 0);
  --i: 1;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.login-body {
  min-width: 420px;
   width: 100%;
  background: url("./images/Bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding: 30px 0;
  /* overflow-x: hidden; */
}

/* ----------login-component------- */

#login {
  width: 100%;
  /* border: 1px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-wrap: wrap; */
  padding: 20px;
}


/* ----------right-div/robo-hand----- */

#robo-hand {
  border: 5px solid var(--pri);
  height: 85vh;
  width: 470px;
  min-width: 380px;
  border-radius: 10px;
  background: url("./images/Robot\ hand.jpg");
  background-repeat: repeat;
  background-position: center center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: lighter;
  letter-spacing: 1px;
  flex-direction: column;
  line-height: 30px;
}

#robo-hand p {
  font-size: small;
  font-weight: bold;
}

/* ----------Both-btn----------- */

#robo-hand button,
.form-div button {
  padding: 10px;
  width: 120px;
  height: 40px;
  font-weight: bold;
  background-color: var(--pri);
  border: none;
  color: #fff;
  border-radius: 5px;
  margin-top: 25px;
  cursor: pointer;
  transition: all 0.2s ease;
  outline: none;
}

.form-div button {
  line-height: 10px;
}

#robo-hand button:hover,
.form-div button:hover {
  background-color: #fff;
  color: var(--pri);
  border: 1px solid var(--pri);
  box-shadow: 2px 4px #888888;
}

#robo-hand button:active,
.form-div button:active {
  background-color: var(--pri);
  color: #fff;
  box-shadow: -4px -2px #888888;
}

.form-div button i {
  font-size: 1.1rem;
  margin-right: 5px;
}

/* ------------INPUT-Section--------- */

#form {
  /* border: 1px solid blue; */
  height: 70vh;
  max-width: 400px;
  width: 400px;
  background: rgb(234, 255, 228);
  border-radius: 7px;
  z-index: 2;
  position: relative;
  left: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.form-div {
  /* border: 1px solid red; */
  height: 70%;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.form-div h4 {
  /* border: 1px solid brown; */
  display: flex;
  align-items: center;
}

.form-div h4 span {
  display: inline-block;
  width: max-content;
  /* border: 1px solid yellow; */
  margin: 3px;
  cursor: pointer;
}

.form-div span div {
  width: 20px;
  height: 20px;
  border: 4px solid var(--pri);
  background-color: var(--pri);
  color: #fff;
}

.form-div div {
  border: none;
  border-left: 3px solid var(--pri);
  height: 8vh;
  min-width:320px ;
  border-radius: 4px;
  background: #fff;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.form-div div .icons {
  font-size: 150%;
  color: rgb(184, 184, 184);
}

.form-div div input {
  width: 250px;
  height: 50%;
  outline: none;
  border: none;
  border-left: 1px solid rgb(184, 184, 184);
  padding: 5px;
  font-style: italic;
}

#forgot-div .check{
  width: 20px;
  height: 20px;
}



#forgot-div .link{
  font-size: small;
  text-align: right;
  color: black;
  margin-left: 60px;
}

.form-div span input[type="checkbox"] {
  accent-color: rgb(0, 161, 0);
  cursor: pointer;
}
.form-div div input[type="checkbox"]:hover {
  accent-color: rgb(0, 161, 0);
}

label {
  color: black;
  min-width: 100px;
  font-size: 14px;
  margin-left: 5px
}

#form h1 {
  width: max-content;
  margin: 0 auto;
}


@media only screen and (max-width:868px) {
  #login {
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  #form {
    position: static;
    margin: 30px 0px !important;
    width: 380px;
  }

  #robo-hand {
    max-width: 80%;
  }
  #forgot-div .link{
    margin-left: 58px;
  }
}

/* -------forgot-password-------- */

.forgot-password{
  min-width: 420px;
  width: 100%;
  background: url("./images/Bg.jpg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  color: #fff;
  padding: 156.2px 0;
  min-height: 100%;
}

.forgot-password .forgot-div{
  border: 1px solid white;
  background-color: rgba(164, 164, 164, 0.09);
  max-width: 400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 40px 0 20px;
  border-radius: 24px;
}

.forgot-password .forgot-div p span{
  color: rgba(25, 252, 0, 0.723);
}

.forgot-password .forgot-div > p{
  margin: 10px 60px ;
  font-weight: lighter;
  font-size: small;
}

.forgot-password .forgot-div .padding{
  padding-left: 72px;
  margin: 30px 0 ;
}

@media (width <= 400px){
  .forgot-password{
    min-width: 400px;
  }

  .forgot-password .forgot-div{
    max-width: 300px;
  }

  .forgot-password .forgot-div .forgot-email{
    min-width:250px;
  }
  .forgot-password .forgot-div .forgot-email input{
    width: 200px;
  }

  .forgot-password .forgot-div > p{
    margin: 10px 20px;
  }
}

/* ------OPT----- */

.forgot-password .otpbox{
  text-align: left;
}
.forgot-password .forgot-div h4{
  width: 70%;
  font-size: larger;
  font-weight: 300;
}

.forgot-password .otpbox .otptext{
  /* border: 1px solid red; */
  padding:  0 12px 0 0;
}

.forgot-password .otpbox div{
  margin: 30px 0 ;
}

.forgot-password .otpbox div .otp-field{
  outline: none;
  border: none;
  width: 40px;
  height: 40px;
  border-radius:10px ;
  margin: 0 10px;
  text-align: center;
}

.forgot-password .otpbox p .otp-next-btn,
.forgot-password .otpbox p .otp-clear-btn{
  border: none;
  font-weight: 900;
  font-size: large;
  padding: 5px 20px;
  border-radius: 7px;
  margin: 0 10px;
  cursor: pointer;
  color: #fff;
  background-color: var(--pri);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.forgot-password .otpbox p .otp-next-btn:active,
.forgot-password .otpbox p .otp-clear-btn:active{
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.forgot-password .otpbox p .otp-next-btn{
  padding: 5px 25px;
}
